.image-uploader-wrapper {
  position: fixed;
  display: flex;
  justify-content: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgb(241, 241, 241);
  padding: 50px 30px;
  z-index: 2;
  overflow: hidden;
  .dashboard-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: auto;
    min-width: 400px;
    width: 80%;
    height: 100%;
    .upload {
      width: 300px;
      p {
        text-align: center;
      }
      label {
        cursor: pointer;
        display: flex;
        justify-content: flex-start;
        max-width: 300px;
        background-color: rgb(255, 255, 255);
        border-radius: 15px;
        border: 1px solid rgba(0, 0, 0, 0.3);
        text-align: center;
        font-weight: 600;
        padding: 10px 10px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        margin-bottom: 20px;
        p {
          margin-right: 5px;
        }
        input[type="file"] {
          display: none;
        }
        .image-name {
          display: block;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
        svg {
          min-height: 25px;
          min-width: 25px;
          max-height: 25px;
          max-width: 25px;
          margin: 0 10px;
        }
      }
      .upload-image-button {
        cursor: pointer;
        margin-bottom: 30px;
        background-color: rgb(255, 255, 255);
        border-radius: 15px;
        border: 1px solid rgba(0, 0, 0, 0.3);
        text-align: center;
        font-weight: 600;
        padding: 10px 10px;
        transition: all 300ms ease-in-out;
        &:hover {
          scale: 1.1;
          box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
        }
      }
      .loader {
        margin: 20px 0;
        text-align: center;
      }
    }
  }
}

.image-to-be-upload {
  text-align: center;
  img {
    width: 160px;
    height: 160px;
    border-radius: 10px;
    object-fit: cover;
  }
}
